.headerContainer{
    display: flex;
    align-items: center;
}
.calendarIcon{
    width: 40px;
    margin: 10px;
}
.headerText{
    font-family: 'DynaPuff', cursive;
    color: rgb(145, 145, 145);
    margin-left: 10px;
}

.headerButton{
    cursor: pointer;
    height:30px;
    color: rgb(90, 90, 90);
    background: white;
   width: 60px;
   margin-left: 30px;
   margin-right: 30px;
   border-radius: 3px;
   border: 0.1px solid rgb(206, 206, 206);
}
.headerButton:hover{
    background-color:rgb(206, 206, 206) ;

}
.calendarArrow{
    width: 20px;
    
   
}
.buttonArrow{
    background-color: white;
    border: none;
    cursor: pointer;
}

.container{
   
    display: flex;
}
.sideBarContainer{
border-top: 0.1px solid rgb(206, 206, 206);
    width: 300px;
}

.sideBarContainerButton{
    margin-top: 20px;
   
}
.smallCalendar{

    margin-top: 20px;
    font-family: 'DynaPuff', cursive;
    color: rgb(145, 145, 145);
   
  
    width:  300px;
    display: flex;
    justify-content: center;
    gap: 40px;
    margin-bottom: 10px;
}
.smallsize{
   
    width: 10px;

}

.smallCalendar p{

    width: 150px;
}
.gapCalendar{
    gap:27px;

}
.small{
   
    width: 275px;
    margin-left: 25px;


   
}
.buttonSmallCalendar{
    font-family: 'DynaPuff', cursive;
    width: 30px;
    height: 30px;
    background-color: white;
    border: none;
    cursor: pointer;
    margin-right: 7px;
    color: rgb(84, 68, 81);
}
.buttonSmallCalendarCurrent{
    background:rgb(84, 68, 81);
    color: white;
    border-radius:50px ;
    
}
.buttonSmallCalendarSelected{
    background:rgb(145, 145, 145);
    color: white;
    border-radius:50px ;

}