.EventModalContainer {
    width: 100%;
    height: 100%;

    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    

    
}
.EventModal{
    width: 25%;
   
  background-color: rgb(255, 255, 255);
  box-shadow: -2px 12px 41px 2px rgba(34, 60, 80, 0.2);
   
}
.headerEventModal{
    height: 30px;
    display: flex;
    justify-content: end;
    background:rgb(235, 235, 235);
}
.closeIcon{
    width: 15px;
   
}
.closeButton{

    border: none;
    background-color: transparent;
    cursor: pointer;
}
.clockIcon{
    width: 20px;
    margin: 0;


}
.clockText{
   
    margin: 0;
}
.dateContainer{
    display: flex;
    width: 100%;
   
    margin-left:15px;
    gap: 10px;
    

}
.inputEvent{
    margin-top: 30px;
    background-color: white;
    border: none;
    border-bottom: 2px solid rgb(211, 210, 210);;
    outline: none;
    color:rgb(84, 68, 81);
    margin-left:15%;
    width:80%;
    margin-bottom: 20px;
    font-weight: bold;
}
.inputEvent:focus{
    border-bottom: 2px solid rgb(84, 68, 81);

}
.checkIcon{
    margin-left:5px;
    width: 10px;
    height: 10px;
}
.footer{
    display: flex;
    justify-content:end;
    margin-bottom: 30px;
   
}