@import url('https://fonts.googleapis.com/css2?family=DynaPuff&display=swap');
.monthContainer{

   display: grid;   
    grid-template-columns:repeat(7,1fr);
    grid-template-rows:repeat(5,1fr);
   
  
   
    width: 100%;
    height:90vh;
    
}
.date{
    
    display: flex;
    justify-content: center;
    text-align: center;
}
.Day{
  
    font-family: 'DynaPuff', cursive;
    color: rgb(84, 68, 81);
   
   border: 0.1px solid rgb(206, 206, 206);
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.currentDay{
    color: white;
    background-color: rgb(84, 68, 81);
    height: 20px;
    padding: 3px;
    border-radius:50px;

}